import { Component, OnInit, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

import { Router } from '@angular/router';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';

@Component({
  selector: 'app-movein-maintain',
  templateUrl: './movein-maintain.component.html',
  styleUrls: ['./movein-maintain.component.scss']
})
export class MoveinMaintainComponent implements OnInit {

  isDataLoaded: boolean = false;

  moveInData: string = "";
  moveInDataList: any;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  isMobile: boolean = false;

  apartmentBlockId: any;
  apartmentBlockUnitId: any;

  blockUnitData: any;

  modalService: any;


  isError: boolean = false;
  alertMessage: string = "";
  selectedInput: string = "";
  columnField: any = {};
  statusNames: any;
  statusType: any = {};

  constructor(
    private injector: Injector,
    private moveInOutService: MoveInOutService,
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private dialog: MatDialog,
    private router: Router
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  onDateChange(event, type) {

    if (event != null) {
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }

  }

  getDate(movein, date) {

    this.moveInDataList.map(obj => {
      if (obj.id == movein.id) {
        obj.inDateData = moment(date).format("DD/MM/YYYY");
      }
    });

    return moment(date).format("DD/MM/YYYY");
  }

  getTime(time) {
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable() {
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }


  getApproveStatus(id) {
    if (this.statusType[id] && id == 372) {
      return 'high';
    }
    else if (this.statusType[id] && id == 373) {
      return 'low';
    }
    else if (this.statusType[id] && id == 374) {
      return 'm-yellow';
    }
    else if (this.statusType[id] && id == 380) {
      return 'grey-850';
    }

  }

  approveMoveIn(data, type) {

    let details = {
      "id": data.id,
      "inDate": data.inDate,
      "inTime": data.inTime,
      "familyCount": data.familyCount,
      "comments": data.comments,
      "isActive": data.isActive,
      "apartmentBlockUnitId": data.apartmentBlockUnitId,
      "reqUserId": data.reqUserId,
      "statusId": type == 'approve' ? 373 : 374,
      "approvedBy": type == 'approve' ? parseInt(this.cookieService.get('userId')) : data.approvedBy,
      "approvedDate": type == 'approve' ? new Date().toISOString() : data.approvedDate,
      "insertedBy": data.insertedBy,
      "insertedOn": data.insertedOn,
      "updatedBy": parseInt(this.cookieService.get('userId')),
      "updatedOn": new Date().toISOString(),
      "apartmentId": data.apartmentId,
      "userName": data.userName,
      "mobile": data.mobile,
      "email": data.email,
      "block_Unit": data.block_Unit,
      "requestType": data.requestType,
      "statusName": type == 'approve' ? "Approved" : "Rejected",
      "serialNo": data.serialNo
    }

    let params = {
      moveIn: details
    }

    this.moveInOutService.updateMoveIn(params).subscribe((res: any) => {

      if (res.message) {
        if (type == 'approve') {
          this.sharedService.setAlertMessage("Movein approved successfully");
          data.statusId = 373;
          data.statusName = 'Approved';
          data.approvedBy = parseInt(this.cookieService.get('userId'));
        } else {
          data.statusId = 374;
          data.statusName = 'Rejected';
          data.approvedBy = null;
        }
      }
      else {
        this.isError = true;
        this.alertMessage = res.errorMessage;
      }

    },
      error => {
        
      });


  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      statusIds: "372,373"
    }

    this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {

      this.moveInDataList = res.filter(item => {
        return item.isActive;
      });
      this.totalItems = this.moveInDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res: any) => {

      this.blockUnitData = res;

    });
    this.getStatusName();
  }

  showMoveInDetails(details): void {
    this.modalService.showMoveInDetails(details);
  }

  chekInUser(data, i) {

    const message = `Do you want to check In?`;

    const dialogData = new ConfirmDialogModel("Confirm Action", message);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let details = {
          "id": data.id,
          "inDate": data.inDate,
          "inTime": data.inTime,
          "familyCount": data.familyCount,
          "comments": data.comments,
          "isActive": data.isActive,
          "apartmentBlockUnitId": data.apartmentBlockUnitId,
          "reqUserId": data.reqUserId,
          "statusId": 375,
          "approvedBy": data.approvedBy,
          "approvedDate": data.approvedDate,
          "insertedBy": data.insertedBy,
          "insertedOn": data.insertedOn,
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "apartmentId": data.apartmentId,
          "userName": data.userName,
          "mobile": data.mobile,
          "email": data.email,
          "block_Unit": data.block_Unit,
          "requestType": data.requestType,
          "statusName": 'Movedin',
          "serialNo": data.serialNo
        }
    
        let params = {
          moveIn: details
        }
    
        this.isDataLoaded = false;
        this.moveInOutService.updateMoveIn(params).subscribe((res: any) => {
          if (res.message) {
            this.moveInDataList.splice(i, 1);
            this.totalItems = this.moveInDataList.length;
            this.sharedService.setAlertMessage("Checked In successfully");
            data.statusId = 375;
            this.isDataLoaded = true;
          }
          else {
            this.isError = true;
            this.isDataLoaded = true;
            this.alertMessage = res.errorMessage;
          }

        },
          error => {
            this.isDataLoaded = true;
          });
      }
    });



  }

  getStatusName() {

    let params = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 92
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

      this.statusNames = res.filter(item => {
        return item.isActive;
      });
      this.statusNames.forEach(element => {
        this.statusType[element.lookupValueId] = element;
      });
    });
  }

  navigateToMovein() {
    this.router.navigate(['ams/moveinout-tracker/create-movein']);
  }

}